/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
.tab {
  display: none;
}
.tab.tab--active {
  display: block;
}
.tabs-nav__item {
  cursor: pointer;
}
html {
  line-height: 1;
  font-size: 14px;
}
body {
  font-family: 'Open Sans', arial, sans-serif;
  color: #262626;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
p {
  margin: 0;
}
a {
  text-decoration: none;
  color: #262626;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
textarea {
  resize: none;
}
section {
  position: relative;
  padding: 30px 0;
}
input,
select,
button {
  outline: none;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
.container {
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
  max-width: 1140px;
}
.logo__img {
  height: 22px;
}
.search {
  position: relative;
}
.search__body {
  display: none;
  width: 550px;
  position: absolute;
  right: 0;
  top: -35px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.21), 0 10px 20px 0 rgba(0, 0, 0, 0.1);
}
.search__icon {
  width: 20px;
  height: 20px;
  fill: #ff6d00;
  cursor: pointer;
}
.search__icon--open .search__body {
  display: block;
}
.search__form {
  display: flex;
  align-items: center;
}
.search__input {
  width: 100%;
  height: 50px;
  line-height: 48px;
  border: 1px solid #dddddd;
  border-radius: 2px 0 0 2px;
  background-color: transparent;
  padding: 0 50px 0 20px;
  color: #4b4b4b;
  border-right: 0;
}
.search__input::placeholder {
  color: #a6a6a6;
}
.search__input-wrap {
  position: relative;
  flex: 1;
}
.search__close {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 0;
  top: 0;
  right: 0;
  width: 40px;
  cursor: pointer;
  z-index: 1;
}
.search__close-icon {
  width: 16px;
  height: 16px;
  fill: rgba(75, 75, 75, 0.5);
}
.search__btn {
  padding: 0 15px;
  flex: 0 0 auto;
  height: 50px;
  background-color: #ff6d00;
  font-weight: 700;
  color: #fff;
  border-radius: 0 4px 4px 0;
  border: 0;
}
.btn {
  padding: 0 20px;
  display: block;
  text-align: center;
  height: 40px;
  border: 1px solid #ff6d00;
  border-radius: 2px;
  line-height: 38px;
  color: #ff6d00;
  font-weight: 600;
  background-color: #fff;
  transition: all linear .3s;
}
.btn--transparent-white {
  color: #fff;
  background-color: transparent;
  border-color: #fff;
}
.btn--transparent-white:hover {
  border-color: #000;
  color: #4b4b4b;
  background-color: #fff;
}
.btn--round {
  border-radius: 20px;
}
.btn--inline {
  display: inline-block;
}
.title-section {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.67;
}
.header {
  border-bottom: 1px solid #eef2f4;
}
.header-top {
  background-color: #eef2f4;
  padding: 10px 0;
}
.header-top__content {
  display: flex;
  align-items: center;
}
.header-nav__list {
  display: flex;
  align-items: center;
}
.header-nav__item {
  margin-right: 30px;
}
.header-nav__item:last-child {
  margin-right: 0;
}
.header-nav__link {
  font-size: 12px;
  line-height: 1.67;
  color: #4b4b4b;
}
.header-nav--right {
  margin-left: auto;
}
.header-main {
  padding: 25px 0;
}
.header-main__content {
  display: flex;
  align-items: center;
}
.header-search {
  margin-left: auto;
}
.header-menu__content {
  display: flex;
  align-items: center;
}
.header-phones {
  display: flex;
  margin-right: 90px;
}
.header-phones > * {
  margin-right: 30px;
}
.header-phones > *:last-child {
  margin-right: 0;
}
.header-lable {
  display: block;
  font-size: 16px;
  font-weight: 600;
  color: #4b4b4b;
  white-space: nowrap;
}
.header-addres {
  display: flex;
  align-items: center;
  flex: 0 0 190px;
  width: 190px;
}
.header-addres__icon {
  flex: 0 0 20px;
  width: 15px;
  height: 21px;
  fill: #a9a9a9;
}
.header-addres > * {
  margin-right: 15px;
}
.header-addres > *:last-child {
  margin-right: 0;
}
.header-addres__text {
  font-size: 12px;
  line-height: 1.67;
  color: #4b4b4b;
}
.header-shop {
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  flex: 0 0 270px;
  width: 270px;
}
.header-shop > * {
  margin-right: 10px;
}
.header-shop > *:last-child {
  margin-right: 0;
}
.header-shop .header-goods__price {
  display: block;
  font-size: 16px;
  font-weight: 600;
  color: #4b4b4b;
  white-space: nowrap;
}
.header-shop .header-goods__count {
  display: block;
  font-size: 12px;
  line-height: 1.67;
  color: #a9a9a9;
}
.header-basket {
  display: flex;
  padding: 0 20px;
  align-items: center;
  height: 40px;
  background-color: #f6f6f6;
  border-radius: 20px;
  cursor: pointer;
}
.header-basket__icon {
  width: 20px;
  height: 20px;
  fill: #ff6d00;
}
.header-basket > * {
  margin-right: 15px;
}
.header-basket > *:last-child {
  margin-right: 0;
}
.header-basket__text {
  color: #4b4b4b;
  font-weight: 600;
}
.header-logo {
  margin-right: 40px;
}
.header-item__link {
  display: block;
  font-size: 12px;
  line-height: 1.67;
  color: #a9a9a9;
}
.header-item__link--underline {
  text-decoration: underline;
}
.main-nav__list {
  display: flex;
  align-items: center;
  position: relative;
}
.main-nav__item {
  margin-right: 40px;
}
.main-nav__item:last-child {
  margin-right: 0;
}
.main-nav__item:hover .main-nav__link {
  border-bottom-color: #ff6d00;
}
.main-nav__item:hover .main-nav__dropdown-wrap {
  display: block;
}
.main-nav__link {
  display: block;
  padding: 15px 0;
  font-size: 16px;
  line-height: 1.88;
  font-weight: 600;
  color: #4b4b4b;
  border-bottom: 2px solid transparent;
}
.main-nav__dropdown {
  display: inline-flex;
  padding-top: 10px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1), 0 2px 5px 0 rgba(0, 0, 0, 0.05);
  background-color: #fff;
}
.main-nav__dropdown-wrap {
  display: none;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  z-index: 5;
}
.main-nav__icon {
  width: 7px;
  height: 7px;
  fill: #4b4b4b;
  margin-left: 10px;
  transform: rotate(90deg);
}
.main-subnav__list {
  position: relative;
}
.main-subnav__list .main-subnav__list {
  position: absolute;
  top: 0;
  left: 360px;
  display: none;
}
.main-subnav__item {
  width: 360px;
}
.main-subnav__item:hover > .main-subnav__link {
  background-color: #eef2f4;
  color: #ff6d00;
}
.main-subnav__item:hover .main-subnav__icon {
  fill: #ff6d00;
}
.main-subnav__item:hover .main-subnav__list {
  display: block;
}
.main-subnav__link {
  display: flex;
  align-items: center;
  line-height: 20px;
  padding: 10px;
}
.main-subnav__icon {
  margin-left: auto;
  color: #ccc;
  width: 8px;
  height: 12px;
}
.banner-wrap {
  position: relative;
  padding: 0 90px;
  height: 300px;
  background-image: linear-gradient(255deg, #ffb800, #ff6d00);
  border-radius: 4px;
}
.banner__nav {
  position: absolute;
  top: 50%;
  margin-top: -25px;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.2);
  cursor: pointer;
  transition: all linear .3s;
}
.banner__nav--prev {
  left: 30px;
}
.banner__nav--next {
  right: 30px;
}
.banner__nav:hover {
  background-color: rgba(255, 255, 255, 0.5);
}
.banner__nav-icon {
  height: 17px;
  width: 10px;
  fill: rgba(0, 0, 0, 0.5);
}
.banner__content {
  display: flex;
  justify-content: center;
  padding: 30px 0;
}
.banner__body {
  color: #fff;
}
.banner__item {
  outline: none;
}
.banner__title {
  display: block;
  font-size: 50px;
  font-family: 'VAGRounded', sans-serif;
  line-height: 1;
  margin-bottom: 15px;
}
.banner__descr {
  display: block;
  margin-bottom: 40px;
  font-size: 18px;
  line-height: 1.67;
}
.banner__img {
  flex: 0 0 40%;
  width: 40%;
  text-align: center;
}
.banner__img img {
  max-width: 100%;
  max-height: 240px;
}
.banner__dot {
  width: 8px;
  height: 8px;
  background-color: rgba(38, 38, 38, 0.3);
  border-radius: 50%;
  display: block;
}
.banner__dots {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  z-index: 2;
  cursor: pointer;
}
.banner__dots .slick-dots {
  display: flex;
  align-items: center;
}
.banner__dots .slick-dots li {
  margin-right: 10px;
}
.banner__dots .slick-dots li:last-child {
  margin-right: 0;
}
.banner__dots .slick-dots li.slick-active .banner__dot {
  background-color: #fff;
}
.advanatages {
  padding: 0;
}
.advanatages__list {
  display: flex;
  padding: 20px;
  align-items: center;
  background-color: #f6f8f9;
  border-radius: 4px;
}
.advanatages__item {
  display: flex;
  align-items: center;
  margin-right: 180px;
}
.advanatages__item:last-child {
  margin-right: 0;
}
.advanatages__body {
  display: flex;
  flex-direction: column;
}
.advanatages__title {
  margin-bottom: 10px;
  font-size: 18px;
}
.advanatages__descr {
  font-size: 12px;
  color: rgba(38, 38, 38, 0.7);
}
.advanatages__icon {
  width: 50px;
  height: 50px;
  fill: #999999;
  margin-right: 20px;
}
.hits__body {
  padding-top: 20px;
}
.hits-nav {
  display: flex;
  align-items: center;
}
.hits-nav__item {
  flex: 1;
  margin-right: 30px;
  height: 50px;
  border-bottom: 2px solid #ddd;
  line-height: 48px;
  text-align: center;
}
.hits-nav__item.tabs-nav__item--active {
  border-bottom-color: #ff6d00;
}
.hits-nav__item:last-child {
  margin-right: 0;
}
.product-prev {
  position: relative;
  padding: 20px;
  padding-top: 190px;
  transition: all linear .3s;
}
.product-prev:hover {
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1), 0 2px 5px 0 rgba(0, 0, 0, 0.05);
}
.product-prev__header {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  height: 160px;
}
.product-prev__img {
  width: 100%;
  max-width: 265px;
  max-height: 150px;
  outline: none;
}
.product-prev__title {
  line-height: 1.43;
  font-weight: 600;
}
.product-prev__body {
  position: relative;
  padding-top: 90px;
  margin-bottom: 20px;
}
.product-prev__info {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.product-prev__price {
  display: flex;
  align-items: baseline;
  margin-bottom: 20px;
}
.product-prev__price > * {
  margin-right: 10px;
}
.product-prev__price > *:last-child {
  margin-right: 0;
}
.product-prev__price-current {
  font-size: 20px;
  font-weight: 600;
  color: #ff6d00;
}
.product-prev__price__old {
  font-size: 13px;
  text-decoration: line-through;
  color: #4b4b4b;
}
.product-prev__colors .slick-dots {
  display: flex;
}
.product-prev__colors .slick-dots li {
  border: 1px solid transparent;
  border-radius: 2px;
}
.product-prev__colors .slick-dots li.slick-active {
  border-color: #4b4b4b;
}
.product-prev__color {
  display: block;
  margin: 1px;
  width: 20px;
  height: 20px;
  border-radius: 2px;
  outline: none;
}
